import React, { useEffect, useState } from 'react'
import { detailsUser, updateUserByAdmin } from '../../actions/userAction';
import { useDispatch, useSelector } from 'react-redux';
import LoadingBox from '../../components/LoadingBox/LoadingBox';
import MessageBox from '../../components/MessageBox/MessageBox';
import { Button, Checkbox, FormControlLabel, TextField } from '@material-ui/core';
import { USER_DETAILS_RESET, USER_UPDATE_RESET,} from '../../constants/userConstants';
import "./UserEdit.scss";


function UserEdit(props) {
    const idUser = props.match.params.id;

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [isAdmin, setIsAdmin] = useState(false);
    const [isSeller, setIsSeller] = useState(false);
    const [successMessage, setSuccessMessage] = useState('');

    const userDetail = useSelector(state => state.userDetail);
    const { user, loading, error } = userDetail;

    const userUpdatedByAdmin = useSelector(state => state.userUpdatedByAdmin);
    const { loading: loadingUpdate, success, error: errorUpdate } = userUpdatedByAdmin;

    const dispatch = useDispatch();

    const submitHandler = (event) => {
        event.preventDefault();
        dispatch(updateUserByAdmin({ idUser, name, email, isAdmin, isSeller }))

    }

    useEffect(() => {
        console.log("load one")
        dispatch({ type: USER_DETAILS_RESET })
    }, [])

    useEffect(() => {
        if (success) {
            console.log("are you here?")
            setSuccessMessage("Successfully updated")
            dispatch({ type: USER_UPDATE_RESET })
            setTimeout(() => {
                setSuccessMessage("")
            }, 4000);
        }
        if (!user?.name) {
            dispatch(detailsUser(idUser));
        } else {
            setName(user.name);
            setEmail(user.email);
            setIsAdmin(user.isAdmin);
            setIsSeller(user.isSeller);
        }

    }, [success, dispatch, idUser, user,]);


    return (
        <div className="form-medium">
            <form onSubmit={submitHandler}>
                <h2>Update User's information</h2>
                <br></br>
                {loading
                    ? <LoadingBox />
                    : error ? <MessageBox error={true}>{error}</MessageBox>
                        :
                        <>
                            {loadingUpdate && <LoadingBox />}
                            {errorUpdate && <MessageBox error={true}>{error}</MessageBox>}
                            {successMessage && <MessageBox success={true}>{successMessage}</MessageBox>}

                            <TextField
                                label="Name"
                                variant="filled"
                                type="text"
                                value={name}
                                onChange={e => setName(e.target.value)}
                            />
                            <TextField
                                label="Email"
                                variant="filled"
                                type="email"
                                value={email}
                                onChange={e => setEmail(e.target.value)}
                            />

                            <div className="user-edit__checkbox">
                                <FormControlLabel
                                    control={<Checkbox
                                        color="primary"
                                        onChange={e => setIsSeller(e.target.checked)}
                                        checked={isSeller}
                                        name="isSeller"
                                    />}
                                    label="Is Seller? "
                                />
                            </div>

                            <div className="user-edit__checkbox">
                                <FormControlLabel
                                    control={<Checkbox
                                        color="primary"
                                        onChange={e => setIsAdmin(e.target.checked)}
                                        checked={isAdmin}
                                        name="isAdmin"
                                    />}
                                    label="Is Admin? "
                                />
                            </div>

                            <Button type="submit" className="btn">Update User</Button>
                        </>
                }

            </form>
        </div>
    )
}

export default UserEdit
