import React, { useEffect } from 'react'
import { summaryOrder } from '../../actions/orderActions';
import { useDispatch, useSelector } from 'react-redux';
import LoadingBox from '../../components/LoadingBox/LoadingBox'
import MessageBox from "../../components/MessageBox/MessageBox"
import Chart from "react-google-charts";
import "./Dashboard.scss"

function Dashboard() {
    const orderSummary = useSelector(state => state.orderSummary);
    const { loading, summary, error } = orderSummary;

    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(summaryOrder());
    }, [dispatch])

    return (
        <div className="Dashboard-page">
            <div>
                <h2>Dashboard</h2>
            </div>
            {loading
                ? <LoadingBox />
                : error
                    ? <MessageBox error={error}>{error} </MessageBox>
                    : <div>
                        <div className="box">
                            <div className="box--item">
                                <div><strong> Users</strong> </div>
                                <div>
                                    {summary.users[0].numUsers}
                                </div>

                            </div>

                            <div className="box--item">
                                <div><strong> Orders</strong> </div>
                                <div>
                                    {summary.orders[0] ? summary.orders[0].numOrders : 0}
                                </div>
                            </div>

                            <div className="box--item">
                                <div><strong>   Sales</strong> </div>
                                <div>
                                    ${summary.orders[0] ? summary.orders[0].totalSales.toFixed(2) : 0}
                                </div>
                            </div>
                        </div>

                        <div>
                            <div>
                                <h3>Sales</h3>
                                {summary.dailyOrders.length === 0 ? <MessageBox>No Sale</MessageBox> :
                                    <Chart
                                        width="100%"
                                        height="400px"
                                        chartType="AreaChart"
                                        loader={<div>Loading Chart</div>}
                                        data={[
                                            ['Date', 'Sales'],
                                            ...summary.dailyOrders.map((x) => [x._id, x.sales]),
                                        ]}
                                    />
                                }
                            </div>

                            <div>
                                <h3>Category</h3>
                                {summary.productCategories.length === 0 ? <MessageBox>No Category</MessageBox> :
                                    <Chart
                                        width="100%"
                                        height="400px"
                                        chartType="PieChart"
                                        loader={<div>Loading Chart</div>}
                                        data={[
                                            ['Category', 'Products'],
                                            ...summary.productCategories.map((x) => [x._id, x.count]),
                                        ]}
                                    />
                                }
                            </div>

                        </div>
                    </div>}
        </div>
    )
}

export default Dashboard
