import React, { useEffect, useRef, useState } from 'react'
import "./ChatBox.scss";
import HelpIcon from '@material-ui/icons/Help';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import { Button } from '@material-ui/core';
import { useSelector } from 'react-redux';
import socketIOClient from 'socket.io-client';

const ENDPOINT = process.env.REACT_APP_BACKEND_URL;

function ChatBox() {
    const [socket, setSocket] = useState(null);
    const [messageBody, setMessageBody] = useState('');
    const [messages, setMessages] = useState([
        { name: 'Admin', body: 'Hello. Please ask your question.' },
    ]);
    const [isOpen, setIsOpen] = useState(false);
    const uiMessagesRef = useRef(null);


    //sign in before chatting
    const userSignin = useSelector(state => state.userSignin);
    const { userInfo } = userSignin;

    const supportHandler = () => {
        setIsOpen(true);
        const sk = socketIOClient(ENDPOINT);
        setSocket(sk);
    }

    useEffect(() => {
       if (uiMessagesRef.current) {
         uiMessagesRef.current.scrollBy({
           top: uiMessagesRef.current.clientHeight,
           left: 0,
           behavior: 'smooth',
         });
       }
       if (socket) {
           console.log("connect to backend")
         socket.emit('onLogin', {
           _id: userInfo._id,
           name: userInfo.name,
           isAdmin: userInfo.isAdmin,
         });
         socket.on('message', (data) => {
           setMessages([...messages, { body: data.body, name: data.name }]);
         });
       }
     }, [messages, isOpen, socket]);
   

    const submitHandler = (event) => {
        event.preventDefault();
        if (!messageBody.trim()) {
            alert('Error. Please type message.');
        } else {
            setMessageBody('');
            setTimeout(() => {
                socket.emit('onMessage', {
                    body: messageBody,
                    name: userInfo.name,
                    isAdmin: userInfo.isAdmin,
                    _id: userInfo._id,
                });
            }, 1000);
        }


    }

    return (
        userInfo ? <div className="chatbox">
            {!isOpen
                ? <Button onClick={supportHandler} className="btn-support">
                    <HelpIcon />
                </Button>
                : <div className="box">
                    <div className="box--item">
                        <div> <strong>Support </strong></div>
                        <div>
                            <button type="button" onClick={e => setIsOpen(false)}>
                                <HighlightOffIcon />
                            </button>
                        </div>
                    </div>
                    <ul>
                        {messages.map((msg, index) => (
                            <li key={index}>
                                <strong>{`${msg.name}: `}</strong> {msg.body}
                            </li>
                        ))}
                    </ul>
                    <div>
                        <form onSubmit={submitHandler}>
                            <input
                                value={messageBody}
                                onChange={(e) => setMessageBody(e.target.value)}
                                type="text"
                                placeholder="type message"
                            />
                            <button type="submit">Send</button>
                        </form>
                    </div>
                </div>}
        </div> : ""
    )
}

export default ChatBox
