import { Button } from '@material-ui/core';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { listUsers } from "../actions/userAction"
import LoadingBox from "../components/LoadingBox/LoadingBox"
import MessageBox from "../components/MessageBox/MessageBox";

function UserList(props) {
    const userList = useSelector(state => state.userList);
    const { loading, users, error } = userList;

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(listUsers());
    }, [dispatch])

    return (
        <div className="container container-table">
            <h1>All users of aMaZ</h1>
            {      loading
                ? <LoadingBox />
                : error
                    ? <MessageBox error={error}>{error}</MessageBox>
                    :
                    <table>
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>NAME</th>
                                <th>EMAIL</th>
                                <th>SELLER</th>
                                <th>ADMIN</th>
                                <th>ACTIONS</th>
                            </tr>

                        </thead>
                        <tbody>
                            {users.map(({ _id, isAdmin, isSeller, email, name }, i) => (
                                <tr key={i}>
                                    <td>{_id}</td>
                                    <td>{name}</td>
                                    <td>{email}</td>
                                    <td>{isSeller ? "Yes" : 'No'}</td>
                                    <td>{isAdmin ? "Yes" : 'No'}</td>
                                    <td>
                                        <Button type="button" className="btn btn-extra"
                                        onClick={e => props.history.push(`/user/edit/${_id}`)}
                                        >Edit</Button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>


                    </table>
            }

        </div>
    )
}

export default UserList
